body,
html {
  font-family: "Rubik", sans-serif;
}

header nav {
  height: 106px;
  align-content: center;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 40px 0px;
}

#courses {
  border-bottom: 4px solid #1a7f78;
}
#courses:hover {
  border: none;
}

ul {
  display: flex;
  gap: 30px;
  justify-content: center;
}
@media only screen and (max-width: 1024px) {
  ul {
    display: flex;
    flex-wrap: wrap;
  }
}
ul li a {
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: -0.5px;
  line-height: 25.6px;
  font-variation-settings: "wdth" 80, "wght" 600;
  text-decoration: none;
  border-bottom: 2px solid;
  color: #000;
}
ul li a:hover {
  border: none;
  color: #000;
}
ul li:nth-child(1) a {
  color: #db2c00;
}
ul li:nth-child(2) a {
  color: #255b98;
}
ul li:nth-child(3) a {
  color: #dd0e42;
}
ul li:nth-child(4) a {
  color: #1a7f78;
}
ul li:nth-child(5) a {
  color: #7b2d6d;
}
ul li:nth-child(6) a {
  color: #1a7f78;
}

main {
  width: 80%;
  margin: 0 auto;
}
main .courses {
  width: 60%;
  margin-left: 18%;
  margin-top: 60px;
  margin-bottom: 120px;
}
main .courses h1 {
  font-size: 80px;
  font-weight: 400;
  letter-spacing: -0.5px;
  line-height: 88px;
  font-stretch: 80px;
  font-variation-settings: "wdth" 80, "wght" 900;
  color: rgb(26, 127, 120);
  padding: 2rem 0rem;
}
@media only screen and (max-width: 700px) {
  main .courses h1 {
    font-size: 75px;
  }
}
@media only screen and (max-width: 650px) {
  main .courses h1 {
    font-size: 70px;
  }
}
@media only screen and (max-width: 600px) {
  main .courses h1 {
    font-size: 65px;
  }
}
@media only screen and (max-width: 550px) {
  main .courses h1 {
    font-size: 60px;
  }
}
main .courses a {
  color: rgb(26, 127, 120);
  font-size: 1.25rem;
}
main .courses a:hover {
  color: #db2c00;
  text-decoration: none;
}
main .courses p {
  font-size: 1.25rem;
  line-height: 1.8rem;
  padding-top: 1rem;
  width: 88%;
}
main .contender {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}
@media only screen and (max-width: 1400px) {
  main .contender {
    display: flex;
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 1024px) {
  main .contender {
    display: flex;
    flex-wrap: wrap;
  }
}
main .contender .box {
  width: 26%;
  padding: 2rem;
  border-radius: 1em;
  box-shadow: 0 0.25em 1em rgba(0, 0, 0, 0.1);
  line-height: 1.6rem;
  height: fit-content;
}
@media only screen and (max-width: 1400px) {
  main .contender .box {
    width: 35%;
  }
}
@media only screen and (max-width: 768px) {
  main .contender .box {
    width: 100%;
  }
}
main .contender .box h1 a {
  font-size: 30px;
  text-decoration: none;
  color: #4d4d4d;
  font-weight: 700;
}
main .contender .box h1 a:hover {
  color: #1a7f78;
}
main .contender .box h3 {
  padding: 0.2rem 0rem 1rem 0rem;
}
main .contender .box p {
  font-size: 1.25rem;
  letter-spacing: -0.5px;
}
main .contender .box p b {
  font-weight: bold;
}
main .contender .box button {
  box-shadow: 0 0.125em 0.5em rgba(0, 0, 0, 0.15);
  background-color: #1a7f78;
  border-radius: 100vw;
  color: #fff;
  border: none;
  padding: 0.95em 1.75em 0.85em;
  display: inline-block;
  line-height: 1;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.1rem;
  margin-top: 1rem;
}
main .contender .box button:hover {
  transform: scale(1.05) translateY(-0.125em);
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  box-shadow: 0 0.5em 2.25em #000;
}

footer {
  margin-top: 240px;
  background-color: #dedede;
  height: 200px;
  display: flex;
  justify-content: center;
  color: #4d4d4d;
}
footer p {
  padding: 40px 0px 10px 0px;
  display: flex;
  justify-content: center;
}
footer ul {
  display: flex;
  gap: 20px;
}
footer ul li a {
  color: #4d4d4d;
  font-weight: 700;
}